.about-wrapper {
    
    width: 100%;
    
    background-color:rgb(22, 22, 31);
    z-index: 20;
    // min-height: 92vh;

    img{
        width:300px;
        height: 300px;
        margin: auto ;
        border-radius:0.5rem;
        object-fit: cover ;
        // border: 2px solid rgb(190, 190, 190);
        
        box-shadow: 1px 1px 1px 1px rgba(114, 115, 135, 0.2);
        -webkit-box-shadow: 1px 1px 1px 1px rgba(114, 115, 135, 0.2);
        -moz-box-shadow: 1px 1px 1px 1px rgba(114, 115, 135, 0.2);
    }
    h3{
        color: #fff;
    }

    p{
        font-size: 1rem;
        color:rgb(114, 115, 135);

        text-align: justify;
        margin: 0;
        line-height: 2rem;
    }
}

